export default {
	data() {
		return {
			info: {},
			api: this.$API
		}
	},
	onLoad() {
		this.getList();
	},
	onShow() {
		this.getList();
	},
	methods: {
		getList() {
			this.$http.get('/User/userInfo')
				.then(res => {
					this.info = res.data;
				})
				.catch(err => {
					uni.showToast({
						icon: 'none',
						title: '请重新登录'
					})
				})
		},
		changeAvatar() {
			let that = this;
			uni.chooseImage({
				count: 1,
				success: res => {
					console.log(res);
					this.$http.uploadFile(res.tempFilePaths[0]).then(res => {
							this.$http.put('/User/changeAvatar', res.data)
								.then(res => {
									if(res.code === 1){
										uni.showToast({
											icon: 'success',
											title: '修改成功!',
											success: res => {
												that.getList();
											}
										})
									}
								})
								.catch(err => {
									uni.showToast({
										icon: 'none',
										title: '系统错误，请稍后再试!'
									})
								})
						})
						.catch(err => {
							uni.showToast({
								icon: 'none',
								title: '系统错误，请稍后再试!'
							})
						})
				}
			})
		},
		enterMyInfo() {
			uni.navigateTo({
				url: 'myInfo'
			})
		},
		enterMyAlbum(){
			uni.navigateTo({
				url: 'myAlbum'
			}) 
		},
		enterMyFriend() {
			uni.navigateTo({
				url: 'myFriend'
			})
		},
		enterMyPet() {
			uni.navigateTo({
				url: 'myPet'
			})
		},
		enterMyCollection() {
			uni.navigateTo({
				url: 'myCollection' 
			})
		},
		// 退出
		exit() {
			uni.showModal({
				title: '提示',
				content: '确认退出登录？',
				success: res => {
					if (res.confirm) {
						uni.removeStorageSync('token');
						uni.removeStorageSync('account');
						uni.redirectTo({
							url: '../login/login'
						})
					}
				}
			})
		}
	}
}
